.whole_AU
{
    height: 100%;
    width: 100%;
    overflow: hidden;
    overflow-y: scroll;
}

.whole_AU_IMG
{
    height: 50%;
    width: 100%;
    position: relative;
}

.whole_AU_IMG img
{
    object-fit: cover;
    height: 100%;
    width: 100%;
    filter: opacity(50%);
}

.staff_des
{
    display: flex;
    
    width: 30%;
}

.close_tab
{
    font-weight: 600;
    color: white;
    padding: 10px;
    transition: transform 0.3s ease; /* Smooth transition */
    width: 20px;
}

.close_tab:hover {
    transform: scale(1.2); /* Scales the div to 1.5 times its original size */
    cursor: pointer; /* Changes the cursor */
  
  }


.staff_des_TXT
{
    padding: 10px;
    color: white;
   
    font-size: 13px;
}

.ceo_des_TXT
{
    padding: 10px;
    color: white;
    text-align: center;
    font-size: 15px;
}

.ceo_part
{
    margin-top: 20px;
}

.staff_img_div
{
    height: 200px;
    width: 200px;
}

.staff_img_div img
{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.WAW_TXT
{
    width: 80%;
    margin: 0 auto;
    color: white;
    margin-top: 10px;
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.9);
}

.OT_HH
{
    color: whitesmoke;
    text-align: center;
    font-size: 30px;
    font-weight: 500;
}

.overlay-text {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: rgb(78, 78, 240);
    font-size: 50px;
    font-weight: 700;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
   
    text-align: center;

    padding: 10px 20px;
  }

  .staff_name
  {
    color: rgb(78, 78, 240);
    font-weight: 500;
    text-align: center;
    font-size: 20px;
  }

  .team
  {
    display: flex;
    justify-content: center;
    
  }

  .ceo_img_DIV
  {
    height: 400px;
    width: 400px;
    margin: 0 auto;
  }

  .ceo_img_DIV img
  {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

