.revs_web
{
    height: 100%;
    width: 40%;
    backdrop-filter: blur(7px);
    background: rgba(0, 0, 0, 0.6);
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.9);
    animation: scaleUp 0.3s ease-in-out;
}

@keyframes scaleUp {
    from {
      transform: scale(0.8);
    }
    to {
      transform: scale(1);
    }
  }

.revs_BG
{
    height: 50%;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 30%;
    width: 100%;
}

.revs_part
{
    height: 80%;
    overflow: hidden;
    overflow-y: scroll;
}

.revs_part::-webkit-scrollbar {
    width: 10px; /* Adjust the scrollbar width */
}

.revs_part::-webkit-scrollbar-thumb {
    background-color: rgba(127, 159, 255, 0.8); /* Customize the thumb color */
    border-radius: 5px; /* Round the corners of the thumb */
}

.PP_REV
{
    height: 30px;
    width: 30px;
    background-color: rgb(74, 74, 74);
    border-radius: 10px;
}

.rev_Email
{
    color: white;
    font-weight: 500;
    font-size: 12px;
}

.rev_Words
{
    font-size: 12px;
    color: white;
}

.reviewrs
{
    padding: 10px;
    display: flex;
    align-items: center;
    gap: 10px;
}

.text_area_place
{
    height: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.text_area_place textarea
{
    background-color: transparent;
    resize: none;
    border-width: 2px;
    border-color: rgb(127, 159, 255);
    border-radius: 10px;
    outline: none;
    padding: 5px;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    width: 70%;
    font-size: 12px;
    height: 50%;
}

.text_area_place textarea::placeholder {
    color: #999; /* Change the color to your desired value */
  }

.go_btn
{
    background-color: rgb(127, 159, 255);
    width: 10%;
    height: 70%;
    color: white;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}