.Contacts_BG_INNER
{
    width: 40%;
    backdrop-filter: blur(7px);
    background: rgba(0, 0, 0, 0.6);
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0px 4px 11px rgba(0, 0, 0, 0.9);
    animation: scaleUp 0.3s ease-in-out;
}

@keyframes scaleUp {
    from {
      transform: scale(0.8);
    }
    to {
      transform: scale(1);
    }
  }


.cont_details
{
    color: rgb(249, 249, 249);
    font-weight: 600;
    padding: 10px;
}

.close_tab_cont
{
    background-color: rgb(58, 58, 58);
    color: white;
    width: 20px;
    height: 20px;
    justify-content: center;
    border-radius: 10px;
    font-size: 12px;
    display: flex;
    align-items: center;
    position: absolute;
    left: 98%;
    top: -10px;
    padding: 5px;
    transition: transform 0.3s ease; /* Smooth transition */
}

.close_tab_cont:hover {
    transform: scale(1.2); /* Scales the div to 1.5 times its original size */
    cursor: pointer; /* Changes the cursor */
  
  }