.whole_Intro
{
    height: 100%;
    width: 100%;
   
}

.BG_IMG
{
    height: 100%;
    width: 100%;
    position: fixed;
    filter: opacity(50%);
}

.IMG_BG
{
    height: 100%;
    width: 100%;
    object-fit: cover;
    
}

.Content
{
    height: 100%;
    width: 100%;
    position: fixed;
    top: 0;
    overflow: hidden;
    overflow-y: scroll;
}

.heading
{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50%;
}

.welcome
{
    color: rgb(78, 78, 240);
    font-size: 100px;
    font-weight: 700;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    
}

.WAW
{
    font-size: 50px;
    text-align: center;
    padding: 10px;
}

.contacts_BG
{
    display: flex;
    justify-content: center;
    width: 100%;
    position: fixed;
    top: 50%;
    margin: 0 auto;
}

.buttons_WEB
{
    padding: 10px;
}

.buttons_WEB button
{
    background-color: transparent;
    color: rgb(170, 170, 255);
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
    border: none;
    padding: 10px;
    transition: transform 0.3s ease; /* Smooth transition */
}

.buttons_WEB button:hover {
    transform: scale(1.2); /* Scales the div to 1.5 times its original size */
    cursor: pointer; /* Changes the cursor */
  
  }

.cont_TAG
{
    
    background-color: white;
}

.image_PORTF
{
    height: 30%;
    width: 30%;
}

.image_PORTF img
{
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.green_CONT
{
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    border-radius: 10px;
    margin: 0 Auto;
    gap: 10px;
}

.cont_txt
{
    width: 70%;
}

.About_us
{
    height: 100%;
    width: 100%;
    backdrop-filter: blur(7px);
    background: rgba(0, 0, 0, 0.6);
    position: fixed;
    top: 0;
    animation: riseUp 0.5s ease-in-out;
}

@keyframes riseUp {
    from {
      opacity: 0;
      transform: translateY(100%);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }